.container {
  width: 100%;
  /* max-width: px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin-top: 60px; */
}

.formInput {
  /* width: 100%; */
  width: 70%;
  padding: 10px 20px;
  border-radius: 15px;
  /* border: 1px solid black; */
  /* border-bottom: 3px solid #10171d; */
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
  text-align: center;
  background-color: white;
  color: black;
  font-weight: 700;
}

.formBtn {
  cursor: pointer;
  width: 50%;
  height: 20%;
  padding: 7px 20px;
  text-align: center;
  font-weight: 500;

  color: #333;
  border: 3px solid #333;
  background-color: transparent;

  border-radius: 15px;

  cursor: pointer;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
}

.formBtn:hover,
.formBtn:active {
  color: white;
  background-color: #333;
}

.cancelBtn {
  cursor: pointer;
  width: 50%;
  height: 20%;
  padding: 7px 20px;

  color: white;
  background-color: tomato;
  border: 3px solid tomato;
  font-weight: 500;

  text-align: center;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
}

.cancelBtn:hover,
.cancelBtn:active {
  color: tomato;
  background-color: white;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 80%;
  min-width: 300px;
  max-width: 700px;

  margin-top: 130px;
  margin-bottom: 30px;
  /* background-color: black; */
}

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;

  min-height: 60px;
  max-height: 120px;
  height: 100px;

  padding: 0px 20px;
  background-color: white;

  font-family: -apple-system;
  /* color: white; */
  border: none;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
}

.factoryInput__input::-webkit-input-placeholder {
  text-justify: center;
}

.dark-mode .factoryInput__input {
  background-color: #212529;
  color: #f8f8f8;
}
.dark-mode .factoryInput__input::-webkit-input-placeholder {
  color: #a7a7a7;
}

.factoryInput__label {
  display: flex;
  justify-content: center;
  align-items: center;

  flex-grow: 1;
  width: 100%;
  height: 50px;
  padding: 0px 0px;
  background-color: white;
  /* color: white; */
  /* border: 1px solid #04aaff; */
  border: none;
  /* border-radius: 10px; */
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  font-weight: 500;
  font-size: 16px;
  /* border-bottom: 1px solid #04aaff; */
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);

  color: #333;
  /* cursor: pointer; */
}

.dark-mode .factoryInput__label {
  background-color: #212529;
  color: #f8f8f8;
}

.factoryInput__label i {
  margin-left: 20px;
  margin-right: 20px;
  cursor: pointer;
  color: #333;
}

.factoryInput__label i:hover {
  color: teal;
}
.dark-mode .factoryInput__label i {
  margin-left: 20px;
  margin-right: 20px;
  cursor: pointer;
  color: #f8f8f8;
}

.dark-mode .factoryInput__label i:hover {
  color: #d22873;
}

.factoryInput__arrow {
  position: relative;
  margin-left: 50%;

  text-align: center;

  background-color: #333;
  height: 50px;

  width: 100%;
  min-width: 100px;
  max-width: 500px;

  border-bottom-right-radius: 10px;
  color: white;

  font-size: 26px;
  font-weight: 800;

  cursor: pointer;
}
.factoryInput__arrow:hover,
.factoryInput__arrow:active {
  background-color: teal;
}

.dark-mode .factoryInput__arrow {
  color: #333;
  background-color: #f8f8f8;
}
.dark-mode .factoryInput__arrow:hover,
.dark-mode .factoryInput__arrow:active {
  background-color: #d22873;
}
.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 300px;
  height: 100%;
  /* min-height: 250px; */
  /* max-height: 400px; */
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
}

.factoryForm__attachment img {
  /* height: 60%; */
  /* min-height: 150px; */
  /* max-height: 300px; */
  margin: 15px 0px;
  max-width: 280px;
  width: 85%;
  height: 85%;
  border-radius: 10px;
}

.factoryForm__clear {
  color: #333;
  cursor: pointer;
  text-align: center;
  padding: 10px 0px;
}
.dark-mode .factoryForm__clear {
  color: #ff9e00;
}
.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

.icon {
