/* 09.19 호버 효과 */
.nweet_PC .icon .tooltip {
  position: absolute;
  top: 0px;
  margin-left: auto;
  background: #fff;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
  font-size: 15px;
  padding: 10px 18px;
  border-radius: 25px;
  color: white;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

